import React from 'react'
import { connect } from 'react-redux';
import { createMessage, sendMessage, getMessages } from '../../Store/actions'

import './style.scss'

class Msgs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            chatUser: { id: undefined, name: undefined },
            value: ''
        };
    }
    UNSAFE_componentWillReceiveProps(nextProps) {

        this.setState({
            ...this.state,
            chatUser: {
                id: nextProps.id,
                name: nextProps.name
            }
        });
    }

    componentDidUpdate(nextProps) {
        const { id } = this.props
        if (nextProps.id !== id) {
            if (id) {
                this.props.getMessages(this.generateID());  
                
            }
        }
    }
    
    generateID = () => {
        let id1 = this.props.currentUser.id;
        let id2 = this.props.id;

        if (id1 > id2) {
            return id2 + id1;

        } else {
            return id1 + id2
        }


    }

    render() {
        let handleChange = (e) => {
            this.props.createMessage(e.target.value)
        }


        let sendMsg = () => {
            let generatedID = this.generateID();
            let message = this.props.message;
            this.props.sendMessage(message, generatedID, this.props.currentUser.id);
            this.props.createMessage("");

        }

        return (
            <>

                <section className={(this.state.chatUser.id) ? "messageDiv" : "displayCenter"}>
                    {
                        (this.state.chatUser.id) ?
                            <>
                                <div>
                                    <ul className="messageList">
                                        {
                                            this.props.messages.map((value, index) => {
                                                return <li key={index} >
                                                    <div className={`${(value.senderId === 'wanN8eRfKbMbFmYB6DS13YKZIhq2') ? 'f-right':'f-left'}`}>
                                                        <div className={`messageContainer ${(!value.senderId === 'wanN8eRfKbMbFmYB6DS13YKZIhq2') ? 'f-right':'f-left'}`}>
                                                            {value.message}
                                                        </div>
                                                        <div className={`span ${(!value.senderId === 'wanN8eRfKbMbFmYB6DS13YKZIhq2') ? 'f-right':'f-left'}`}>
                                                            {value.time}
                                                        </div>
                                                    </div>
                                                </li>
                                            })
                                        }
                                    </ul>
                                    {/* <h1>
                                        {this.props.name}
                                    </h1>
                                    <p>
                                        {
                                            this.props.id
                                        }
                                    </p> */}
                                </div>

                                <div className="inputDiv">
                                    <input type="text" value={this.props.message} placeholder="Type your message here..." onChange={(e) => handleChange(e)} />
                                    <span className="sendBtnDiv">
                                        <button className="btn" onClick={() => sendMsg()}>
                                            SEND
                            </button>
                                    </span>
                                </div>
                            </> : <h1>Hey enjoy the Chat</h1>
                    }
                </section>
            </>
        )
    }
}

const mapStateToProps = (state) => {

    return {
        message: state.message,
        currentUser: state.currentUser,
        messages: state.messages
    }
}
const dispatchStateToProps = (dispatch) => {

    return {
        createMessage: (message) => dispatch(createMessage(message)),
        sendMessage: (message, generatedID, cU_id) => dispatch(sendMessage(message, generatedID, cU_id)),
        getMessages: (generatedID) => dispatch(getMessages(generatedID))
    }
}

export default connect(mapStateToProps, dispatchStateToProps)(Msgs);

