import { ADD_CURRENT_USER, SIGNOUT_USER, GET_USERS, CREATE_MESSAGE,SET_MESSAGES } from '../Types'

const initialState = {
    users: JSON.parse(localStorage.getItem("PersistedState")),
    currentUser: { id: localStorage.getItem("uid"), name: localStorage.getItem("name"), email: localStorage.getItem("email"), Photo: localStorage.getItem("Photo") },
    message: '',
    messages: []
}


export default (state = initialState, action) => {
    let { type, payload } = action;

    switch (type) {

        case ADD_CURRENT_USER:
            return {
                ...state,
                currentUser: payload
            }
        case SIGNOUT_USER: {
            return {
                ...state,
                currentUser: payload
            }
        }
        case GET_USERS: {
            return {
                ...state,
                users: payload
            }
        }
        case CREATE_MESSAGE: {
            return {
                ...state,
                message: payload
            }
        }
        case SET_MESSAGES: {
            return {
                ...state,
                messages: payload
            }
        }
        default:
            {
                return state
            }

    }

}