import React from 'react'
import { connect } from 'react-redux'
import { SignOut, getMessages } from '../../Store/actions'
import User from '../../components/Users';
import Msgs from '../../components/Messages/index';
import loadingUsers from '../../Images/loadingUsers.gif'
import './style.scss'

class Messages extends React.Component {

    constructor() {
        super();
        this.state = {
            users: [],
            chatUser:{}
        }

    }
    
    componentDidMount() {
        this.setState({
            ...this.state,
            users: this.props.users
        });
    }
    
    render() {
       
        
        let { name, Photo } = this.props.currentUser;
        return (
            <>
                <div className="navBar">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-6">
                                <User name={name} Photo={Photo} />
                            </div>
                            <div className="col-sm-6">
                                <div className="SignOutDiv">
                                    <button className="btn " onClick={() => this.props.SignOut(this.props.history)}>Sign Out</button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div className="sideBar">
                    {
                        (this.state.users)?
                        this.state.users.map((value, index) => {
                            return (value.id !== this.props.currentUser.id) ? <div key={index} className="UsersAvailableDiv" onClick={()=>this.setState({...this.state,chatUser:{name:value.name,id:value.id}})} >
                                <User name={value.name} sizeSmall={true} Photo={value.Photo}  />
                            </div>:''
                        })
                        : <>
                        <div className="displayCenter">
                            <img width="300px" height="auto" src={loadingUsers} alt=""/>
                        </div>
                        </> 
                    }
                </div>
                <div className="MessagesDiv">
                    <Msgs name={this.state.chatUser.name} id={this.state.chatUser.id}/>
                </div>


            </>
        )

    }
}


const mapStateToProps = (state) => ({
    currentUser: state.currentUser,
    users: state.users
})

const mapDispatchToProps = (dispatch) => {
    return {
        SignOut: (history) => dispatch(SignOut(history)),
        getMessages: (generatedID) => dispatch(getMessages(generatedID))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Messages);