import React from 'react';
import './style.scss'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { AddUser, getUsersList} from '../../Store/actions'




class Home extends React.Component {
    constructor() {
        super();
        this.state= {
            currentUser:{
                name:''
            }
        };
    }
    componentDidMount(){        
        
        this.props.getUsersList();
        localStorage.setItem("PersistedState",JSON.stringify(this.props.users));
        }
        
        render() {

        let { name, email, Photo, id } = this.props.currentUser
        return (
            <>
                <div className="mainDiv">
                    <div className="LoginDiv">
                        <center>
                            {
                                (!id) ? <> <h1>
                                    Let's Chat with Friends
                             </h1>
                                    <hr />
                                    <button className="btn btn-dark" onClick={() => this.props.fbLogin()}>
                                        Login from Facebook
                            </button>
                                </> : ""
                            }
                            {(this.props.currentUser.id) &&
                                <div className="UserInfo">
                                    <h4>{name}</h4>
                                    <h6>Logged in as : {email}</h6>
                                    <img className="UserPhoto" src={Photo} alt="Human" />
                                    <div className="btnDiv">
                                        <Link to="/Messages"> <button className="btn" >
                                            Continue Chat
                                         </button>
                                        </Link>

                                    </div>


                                </div>

                            }
                        </center>
                    </div>

                </div>
            </>
        )
    }

}

const stateMapToProps = (state) => ({
    users: state.users,
    currentUser: state.currentUser
})

const mapDispatchToProps = (dispatch) => ({
    fbLogin: () => dispatch(AddUser()),
    getUsersList: ()=> dispatch(getUsersList())
})

export default connect(stateMapToProps, mapDispatchToProps)(Home);