import firebase from '../../config/firebase.js'
import { ADD_CURRENT_USER, SIGNOUT_USER, GET_USERS, CREATE_MESSAGE, SET_MESSAGES} from '../Types'
const AddUser = () => {

    return (dispatch) => {

        var provider = new firebase.auth.FacebookAuthProvider();
        firebase.auth().signInWithPopup(provider).then(function (result) {
            var user = result.user;
            let obj = {
                id: user.uid,
                name: user.displayName,
                email: user.email,
                Photo: user.photoURL
            }

            firebase.database().ref(`/users/${user.uid}`).set(obj)
                .then(() => {
                    dispatch({ type: ADD_CURRENT_USER, payload: obj });
                    localStorage.setItem("uid", user.uid);
                    localStorage.setItem("name", obj.name);
                    localStorage.setItem("email", obj.email);
                    localStorage.setItem("Photo", obj.Photo);

                }).catch((error) => {
                    console.log(error);
                })
        }).catch(function (error) {
            var errorMessage = error.message;
            console.log("Error==>", errorMessage);
        });
    }
}

const AddCurrentUser = (data) => {
    return (dispatch) =>
        dispatch({
            type: ADD_CURRENT_USER,
            payload: data
        });
}

const SignOut = (history) => {
    return (dispatch) => {
        firebase.auth().signOut().then(function () {
            localStorage.setItem("uid", "");
            localStorage.setItem("name", "");
            localStorage.setItem("email", "");
            localStorage.setItem("Photo", "");
            dispatch({
                type: SIGNOUT_USER, payload: {
                    id: '', name: '', email: '', Photo: ''
                }
            });
            localStorage.setItem("PersistedState", "");
            history.push('/');

        }).catch(function (error) {
            console.log("Error ==> ", error);

        });
    }
}



const getUsersList = () => {

    let arr = [];
    return (dispatch) => {
        firebase.database().ref('/').child('users').on('child_added', (snapshot) => {
            arr.push(snapshot.val())
        })
        dispatch({
            type: GET_USERS,
            payload: arr
        })
    }
}


const createMessage = (message) => {

    return (dispatch) => {
        dispatch({
            type: CREATE_MESSAGE,
            payload: message
        })
    }
}

let current = new Date();

const sendMessage = (message, generatedID, currentUserID) => {
    var hour=(current.getHours())-12;
    var Minutes=current.getMinutes();
    var seconds=current.getSeconds();
    var time =`${hour}:${Minutes}:${seconds}`;

    return (dispatch) => {

        firebase.database().ref('/').child(`messages/${generatedID}`).push({
            message: message,
            senderId: currentUserID,
            time:time
        })
    }
}


const getMessages = (id) => {
    let messages=[];
    return (dispatch) => {
        firebase.database().ref(`/messages/${id}`).on("child_added", (snapshot) => {
            messages.push(snapshot.val())
            dispatch({
                type: SET_MESSAGES,
                payload: messages
            })
        })
    }

}

export { AddUser, AddCurrentUser, SignOut, getUsersList, createMessage, sendMessage, getMessages };