import React from 'react'
import './style.scss'
class User extends React.Component {

    render() {
        let { Photo , name ,sizeSmall }=this.props;
        return (
            <>
                <div className="UserLogo">
                    <img src={Photo} alt="User" />
                    {(!sizeSmall) ? <h4>{name}</h4>: <h6> {name}</h6> }
                </div>


            </>
        )
    }
}

export default User;